.lebify-button-cedar-dark:hover:not(.lebify-button-disabled),
.lebify-button-sea:hover:not(.lebify-button-disabled),
.lebify-button-coast:hover:not(.lebify-button-disabled),
.lebify-button-azure:hover:not(.lebify-button-disabled),
.lebify-button-stone:hover:not(.lebify-button-disabled),
.lebify-button-earth:hover:not(.lebify-button-disabled),
.lebify-button-terracotta:hover:not(.lebify-button-disabled),
.lebify-button-urban:hover:not(.lebify-button-disabled),
.lebify-button-concrete:hover:not(.lebify-button-disabled),
.lebify-button-steel:hover:not(.lebify-button-disabled),
.lebify-button-olive:hover:not(.lebify-button-disabled),
.lebify-button-saffron:hover:not(.lebify-button-disabled),
.lebify-button-grape:hover:not(.lebify-button-disabled),
.lebify-button-festive:hover:not(.lebify-button-disabled),
