}

/* Active states for all new button types */
.lebify-button-cedar:active:not(.lebify-button-disabled),
.lebify-button-cedar-light:active:not(.lebify-button-disabled),
.lebify-button-cedar-dark:active:not(.lebify-button-disabled),
.lebify-button-sea:active:not(.lebify-button-disabled),
.lebify-button-coast:active:not(.lebify-button-disabled),
.lebify-button-azure:active:not(.lebify-button-disabled),
.lebify-button-stone:active:not(.lebify-button-disabled),
.lebify-button-earth:active:not(.lebify-button-disabled),
.lebify-button-terracotta:active:not(.lebify-button-disabled),
.lebify-button-urban:active:not(.lebify-button-disabled),
.lebify-button-concrete:active:not(.lebify-button-disabled),
