/* =============================================== */
/* ================ Loading styles ================ */
/* =============================================== */

/* Loading button styles */
.lebify-button-loading {
    cursor: wait;
    position: relative;
    overflow: hidden;
    transition: all 0.2s ease-in-out;
    color: white !important;
}

[data-theme="light"] .lebify-button-loading .lebify-button-ghost {
    color: #000 !important;
}

[data-theme="dark"] .lebify-button-loading .lebify-button-ghost {
    color: #fff !important;
}

[data-theme="light"] .lebify-button-loading.lebify-button-outlined {
    color: #000 !important;
}

[data-theme="dark"] .lebify-button-loading.lebify-button-outlined {
    color: #fff !important;
}

/* Content transition for smooth loading state change */
.lebify-button-content {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
}

.lebify-button-loading .lebify-button-content {
    opacity: 0.8;
}

/* Button text container */
.lebify-button-text {
    transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
}

/* Spinner-only loading state */
.lebify-button-loading-no-text .lebify-button-content {
    justify-content: center;
}

.lebify-button-loading-no-text .lebify-button-loading-spinner {
    margin: 0 !important;
}

/* Disable pointer events during loading */
.lebify-button-loading * {
    pointer-events: none;
}

/* Loading spinner container */
.lebify-button-loading-spinner {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transform: scale(0.8);
    transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
}

.lebify-button-loading .lebify-button-loading-spinner {
    opacity: 1;
    transform: scale(1);
}

/* Base spinner styles */
.lebify-spinner {
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

/* Spinner sizes */
.lebify-spinner-small {
    width: 16px;
    height: 16px;
}

.lebify-spinner-medium {
    width: 20px;
    height: 20px;
}

.lebify-spinner-large {
    width: 24px;
    height: 24px;
}

/* Circle spinner */
.lebify-spinner-circle svg {
    animation: lebify-spinner-rotate 1.4s linear infinite;
}

.lebify-spinner-circle circle {
    stroke: currentColor;
    stroke-dasharray: 80, 200;
    stroke-dashoffset: 0;
    animation: lebify-spinner-dash 1.4s ease-in-out infinite;
}

@keyframes lebify-spinner-rotate {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes lebify-spinner-dash {
    0% {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
    }

    50% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -35px;
    }

    100% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -124px;
    }
}

/* Dots spinner */
.lebify-spinner-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
}

.lebify-spinner-dots .dot {
    width: 6px;
    height: 6px;
    background-color: currentColor;
    border-radius: 50%;
    display: inline-block;
    animation: lebify-spinner-bounce 1.4s infinite ease-in-out both;
}

.lebify-spinner-dots .dot:nth-child(1) {
    animation-delay: -0.32s;
}

.lebify-spinner-dots .dot:nth-child(2) {
    animation-delay: -0.16s;
}

@keyframes lebify-spinner-bounce {

    0%,
    80%,
    100% {
        transform: scale(0);
    }

    40% {
        transform: scale(1);
    }
}

/* Pulse spinner */
.lebify-spinner-pulse .pulse {
    width: 100%;
    height: 100%;
    background-color: currentColor;
    border-radius: 50%;
    animation: lebify-spinner-pulse 1.2s infinite cubic-bezier(0.4, 0, 0.6, 1);
}

@keyframes lebify-spinner-pulse {
    0% {
        transform: scale(0.8);
        opacity: 0.8;
    }

    50% {
        transform: scale(1);
        opacity: 0.5;
    }

    100% {
        transform: scale(0.8);
        opacity: 0.8;
    }
}

/* Bars spinner */
.lebify-spinner-bars {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
}

.lebify-spinner-bars .bar {
    width: 3px;
    height: 100%;
    background-color: currentColor;
    border-radius: 1px;
    display: inline-block;
    animation: lebify-spinner-scale 1.2s infinite ease-in-out both;
}

.lebify-spinner-bars .bar:nth-child(1) {
    animation-delay: -0.36s;
}

.lebify-spinner-bars .bar:nth-child(2) {
    animation-delay: -0.24s;
}

.lebify-spinner-bars .bar:nth-child(3) {
    animation-delay: -0.12s;
}

@keyframes lebify-spinner-scale {

    0%,
    80%,
    100% {
        transform: scaleY(0.4);
    }

    40% {
        transform: scaleY(1);
    }
}

/* Ellipsis spinner */
.lebify-spinner-ellipsis {
    display: flex;
    align-items: center;
    justify-content: center;
}

.lebify-spinner-ellipsis .dot {
    width: 4px;
    height: 4px;
    margin: 0 2px;
    background-color: currentColor;
    border-radius: 50%;
    display: inline-block;
    animation: lebify-spinner-fade 1.4s infinite ease-in-out both;
}

.lebify-spinner-ellipsis .dot:nth-child(1) {
    animation-delay: -0.32s;
}

.lebify-spinner-ellipsis .dot:nth-child(2) {
    animation-delay: -0.16s;
}

@keyframes lebify-spinner-fade {

    0%,
    80%,
    100% {
        opacity: 0.3;
    }

    40% {
        opacity: 1;
    }
}

/* Prevent width changes during loading state */
.lebify-button.lebify-button-loading {
    min-width: var(--button-width, auto);
}

/* Special handling for icon-only buttons */
.lebify-button-icon-only.lebify-button-loading .lebify-button-loading-spinner {
    margin: 0;
}

/* Center spinner in icon-only buttons */
.lebify-button-icon-only .lebify-button-content {
